body {
    font-family: 'Orbitron', sans-serif;
    background-color: #000000;
    color: #00ff00;
    margin: 0;
    padding: 0;
    overflow-x: hidden;
}



/*loadr */
#loader-wrapper {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #000000;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 9999;
}
.loader-content {
    text-align: center;
}
.cyber-spinner {
    width: 100px;
    height: 100px;
    border: 3px solid transparent;
    border-top: 3px solid #00fffc;
    border-right: 3px solid #ff00fc;
    border-radius: 50%;
    animation: spin 1s linear infinite, glow 2s ease-in-out infinite alternate;
}
.cyber-spinner-center{
    display: flex;
    justify-content: center;
    align-items: center;
}
.loading-text {
    color: #00fffc;
    font-family: 'Orbitron', sans-serif;
    font-size: 24px;
    margin-top: 20px;
    letter-spacing: 5px;
    animation: glitch 1s linear infinite alternate;
}

.progress-bar {
    width: 200px;
    height: 5px;
    background: rgba(0, 255, 252, 0.2);
    margin: 20px auto;
    position: relative;
    overflow: hidden;
}
.progress {
    width: 0;
    height: 100%;
    background: #00fffc;
    position: absolute;
    left: 0;
    top: 0;
    animation: progress 3s ease-in-out forwards;
}
.error-message {
    display: none;
    text-align: center;
}
.glitch-text {
    color: #ff00fc;
    font-family: 'Orbitron', sans-serif;
    font-size: 36px;
    position: relative;
    animation: glitch 0.5s infinite;
}
.glitch-text::before,
.glitch-text::after {
    content: attr(data-text);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}
.glitch-text::before {
    left: 2px;
    text-shadow: -2px 0 #00fffc;
    clip: rect(44px, 450px, 56px, 0);
    animation: glitch-anim 5s infinite linear alternate-reverse;
}
.glitch-text::after {
    left: -2px;
    text-shadow: -2px 0 #ff00fc;
    clip: rect(44px, 450px, 56px, 0);
    animation: glitch-anim2 5s infinite linear alternate-reverse;
}
.sub-text {
    color: #00fffc;
    font-family: 'Orbitron', sans-serif;
    font-size: 18px;
    margin-top: 10px;
}
.retry-btn {
    background: transparent;
    border: 2px solid #00fffc;
    color: #00fffc;
    font-family: 'Orbitron', sans-serif;
    font-size: 16px;
    padding: 10px 20px;
    margin-top: 20px;
    cursor: pointer;
    transition: all 0.3s ease;
}
.retry-btn:hover {
    background: #00fffc;
    color: #000000;
}
@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}
@keyframes glow {
    0% { box-shadow: 0 0 5px #00fffc; }
    100% { box-shadow: 0 0 20px #00fffc, 0 0 40px #ff00fc; }
}
@keyframes glitch {
    0% { text-shadow: 2px 0 #ff00fc, -2px 0 #00fffc; }
    25% { text-shadow: -2px 0 #ff00fc, 2px 0 #00fffc; }
    50% { text-shadow: 2px 0 #00fffc, -2px 0 #ff00fc; }
    75% { text-shadow: -2px 0 #00fffc, 2px 0 #ff00fc; }
    100% { text-shadow: 2px 0 #ff00fc, -2px 0 #00fffc; }
}
@keyframes progress {
    0% { width: 0; }
    100% { width: 100%; }
}
@keyframes glitch-anim {
    0% { clip: rect(11px, 9999px, 90px, 0); }
    5% { clip: rect(94px, 9999px, 53px, 0); }
    10% { clip: rect(39px, 9999px, 90px, 0); }
    15% { clip: rect(86px, 9999px, 6px, 0); }
    20% { clip: rect(94px, 9999px, 81px, 0); }
    25% { clip: rect(7px, 9999px, 78px, 0); }
    30% { clip: rect(49px, 9999px, 34px, 0); }
    35% { clip: rect(3px, 9999px, 83px, 0); }
    40% { clip: rect(68px, 9999px, 96px, 0); }
    45% { clip: rect(59px, 9999px, 70px, 0); }
    50% { clip: rect(29px, 9999px, 98px, 0); }
    55% { clip: rect(22px, 9999px, 18px, 0); }
    60% { clip: rect(79px, 9999px, 3px, 0); }
    65% { clip: rect(13px, 9999px, 66px, 0); }
    70% { clip: rect(34px, 9999px, 78px, 0); }
    75% { clip: rect(50px, 9999px, 81px, 0); }
    80% { clip: rect(83px, 9999px, 43px, 0); }
    85% { clip: rect(100px, 9999px, 92px, 0); }
    90% { clip: rect(9px, 9999px, 5px, 0); }
    95% { clip: rect(10px, 9999px, 33px, 0); }
    100% { clip: rect(60px, 9999px, 74px, 0); }
}
@keyframes glitch-anim2 {
    0% { clip: rect(65px, 9999px, 99px, 0); }
    5% { clip: rect(69px, 9999px, 67px, 0); }
    10% { clip: rect(5px, 9999px, 57px, 0); }
    15% { clip: rect(15px, 9999px, 36px, 0); }
    20% { clip: rect(57px, 9999px, 98px, 0); }
    25% { clip: rect(33px, 9999px, 4px, 0); }
    30% { clip: rect(72px, 9999px, 76px, 0); }
    35% { clip: rect(67px, 9999px, 93px, 0); }
    40% { clip: rect(25px, 9999px, 5px, 0); }
    45% { clip: rect(22px, 9999px, 73px, 0); }
    50% { clip: rect(67px, 9999px, 8px, 0); }
    55% { clip: rect(38px, 9999px, 87px, 0); }
    60% { clip: rect(87px, 9999px, 52px, 0); }
    65% { clip: rect(30px, 9999px, 81px, 0); }
    70% { clip: rect(90px, 9999px, 42px, 0); }
    75% { clip: rect(89px, 9999px, 24px, 0); }
    80% { clip: rect(91px, 9999px, 72px, 0); }
    85% { clip: rect(11px, 9999px, 77px, 0); }
    90% { clip: rect(44px, 9999px, 35px, 0); }
    95% { clip: rect(54px, 9999px, 45px, 0); }
    100% { clip: rect(89px, 9999px, 74px, 0); }
}
/* Header Section */
.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 2rem;
    background-color: rgba(0, 0, 0, 0.8);
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1000;
}

.logo {
    font-size: 1.5rem;
    font-weight: bold;
    color: #00fffc;
}
img.logo.logo-img {
    width: 12px;
}
.nav-menu {
    display: flex;
    justify-content: center;
    align-items: center;
    list-style-type: none
}
.nav-link.active {
    color: #00fffc; /* Cyberpunk cyan color */
    text-shadow: 0 0 5px #00fffc, 0 0 10px #00fffc;
}

.navbar.scrolled {
    background-color: rgba(10, 10, 10, 0.9); /* Dark background with some transparency */
    box-shadow: 0 2px 10px rgba(0, 255, 252, 0.1); /* Cyan shadow for cyberpunk effect */
}
.nav-link {
    color: #00fffc;
    text-decoration: none;
    font-size: 1.1rem;
    transition: all 0.3s ease;
    padding: 0.5rem 1rem;
}

.nav-link:hover {
    color: #ff00fc;
}

.hamburger {
    display: none;
    cursor: pointer;
}

.bar {
    display: block;
    width: 25px;
    height: 3px;
    margin: 5px auto;
    -webkit-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
    background-color: #00fffc;
}


/* Home Section */
section {
    min-height: 60vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 2rem;
}


#home {
    position: relative;
    overflow: hidden;
    background-color: #000000;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
}

.cyber-grid {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.grid-line {
    position: absolute;
    background-color: rgba(0, 255, 255, 0.1);
}

.grid-line.horizontal {
    width: 100%;
    height: 1px;
    animation: horizontalMove 20s linear infinite;
}

.grid-line.vertical {
    width: 1px;
    height: 100%;
    animation: verticalMove 20s linear infinite;
}

@keyframes horizontalMove {
    0% { transform: translateY(-100%); }
    100% { transform: translateY(100%); }
}

@keyframes verticalMove {
    0% { transform: translateX(-100%); }
    100% { transform: translateX(100%); }
}

#particles-js {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 1;
}

.content-wrapper {
    position: relative;
    z-index: 2;
    text-align: center;
}


.glitch {
    font-size: 5rem;
    font-weight: bold;
    text-transform: uppercase;
    position: relative;
    text-shadow: 0.05em 0 0 #00fffc, -0.03em -0.04em 0 #fc00ff,
                 0.025em 0.04em 0 #fffc00;
    /* animation: glitch 725ms infinite; */
}

.cyber-subtitle {
    font-size: 1.5rem;
    margin: 1rem 0;
    color: #00fffc;
}

.bracket {
    color: #ff00fc;
    font-weight: bold;
}

.cyber-button {
    position: relative;
    display: inline-block;
    padding: 15px 30px;
    color: #00fffc;
    text-transform: uppercase;
    letter-spacing: 4px;
    text-decoration: none;
    font-size: 24px;
    overflow: hidden;
    transition: 0.2s;
    margin-top: 2rem;
    cursor: pointer;
}

.cyber-button:hover {
    color: #255784;
    background: #00fffc;
    box-shadow: 0 0 10px #00fffc, 0 0 40px #00fffc, 0 0 80px #00fffc;
    transition-delay: 1s;
}

.cyber-button span {
    position: absolute;
    display: block;
}

.cyber-button span:nth-child(1) {
    top: 0;
    left: -100%;
    width: 100%;
    height: 2px;
    background: linear-gradient(90deg, transparent, #00fffc);
}

.cyber-button:hover span:nth-child(1) {
    left: 100%;
    transition: 1s;
}

.cyber-button span:nth-child(2) {
    top: -100%;
    right: 0;
    width: 2px;
    height: 100%;
    background: linear-gradient(180deg, transparent, #00fffc);
}

.cyber-button:hover span:nth-child(2) {
    top: 100%;
    transition: 1s;
    transition-delay: 0.25s;
}

.cyber-button span:nth-child(3) {
    bottom: 0;
    right: -100%;
    width: 100%;
    height: 2px;
    background: linear-gradient(270deg, transparent, #00fffc);
}

.cyber-button:hover span:nth-child(3) {
    right: 100%;
    transition: 1s;
    transition-delay: 0.5s;
}

.cyber-button span:nth-child(4) {
    bottom: -100%;
    left: 0;
    width: 2px;
    height: 100%;
    background: linear-gradient(360deg, transparent, #00fffc);
}

.cyber-button:hover span:nth-child(4) {
    bottom: 100%;
    transition: 1s;
    transition-delay: 0.75s;
}

.cyber-circuits {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    overflow: hidden;
    line-height: 0;
    z-index: 1;
}

.cyber-circuits svg {
    position: relative;
    display: block;
    width: calc(100% + 1.3px);
    height: 150px;
}

.cyber-circuits path {
    fill: #00fffc;
    opacity: 0.1;
}

.floating-icons {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    overflow: hidden;
    z-index: 1;
}

.icon {
    position: absolute;
    color: #00fffc;
    opacity: 0.3;
    animation: floatIcon 10s ease-in-out infinite;
}

.icon-code {
    top: 20%;
    left: 10%;
    font-size: 3rem;
}

.icon-database {
    top: 60%;
    right: 15%;
    font-size: 2.5rem;
}

.icon-cloud {
    bottom: 30%;
    left: 20%;
    font-size: 2rem;
}

@keyframes floatIcon {
    0%, 100% { transform: translateY(0) rotate(0deg); }
    50% { transform: translateY(-20px) rotate(10deg); }
}
.social-links {
    margin-top: 2rem;
    display: flex;
    justify-content: center;
    gap: 1rem;
}

.social-icon {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color: rgba(0, 255, 252, 0.1);
    display: flex;
    justify-content: center;
    align-items: center;
    color: #00fffc;
    font-size: 1.5rem;
    text-decoration: none;
    transition: all 0.3s ease;
    position: relative;
    overflow: hidden;
}

.social-icon::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 255, 252, 0.2);
    transform: translateY(100%);
    transition: all 0.3s ease;
    z-index: -1;
}

.social-icon:hover::before {
    transform: translateY(0);
}

.social-icon:hover {
    color: #000000;
    transform: translateY(-3px);
    box-shadow: 0 0 15px rgba(0, 255, 252, 0.5);
}

@keyframes neonPulse {
    0%, 100% {
        box-shadow: 0 0 5px rgba(0, 255, 252, 0.5),
                    0 0 10px rgba(0, 255, 252, 0.5);
    }
    50% {
        box-shadow: 0 0 20px rgba(0, 255, 252, 0.8),
                    0 0 30px rgba(0, 255, 252, 0.8);
    }
}

.social-icon:hover {
    animation: neonPulse 1.5s infinite;
}

