  /* Experience Section */
  #experience {
    background-color: #000000;
    padding: 4rem 0;
}

.timeline {
    position: relative;
    max-width: 1200px;
    margin: 0 auto;
    padding: 2rem 0;
}

.timeline::before {
    content: '';
    position: absolute;
    top: 0;
    left: 50%;
    width: 2px;
    height: 100%;
    background: linear-gradient(to bottom, #00fffc, #ff00fc);
    box-shadow: 0 0 10px rgba(0, 255, 252, 0.5);
}

.timeline-item {
    padding: 2rem 0;
    position: relative;
    width: 50%;
    clear: both;
}

.timeline-item:nth-child(odd) {
    float: left;
    padding-right: 2rem;
    text-align: right;
}

.timeline-item:nth-child(even) {
    float: right;
    padding-left: 2rem;
    text-align: left;
}

.timeline-icon {
    position: absolute;
    top: 1rem;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color: #000000;
    border: 2px solid #00fffc;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #00fffc;
    font-size: 1.5rem;
    z-index: 1;
    box-shadow: 0 0 10px rgba(0, 255, 252, 0.5);
}

.timeline-item:nth-child(odd) .timeline-icon {
    right: -10px;
}

.timeline-item:nth-child(even) .timeline-icon {
    left: -10px;
}

.timeline-content {
    padding: 1.5rem;
    background-color: rgba(0, 255, 252, 0.1);
    border: 1px solid #00fffc;
    border-radius: 5px;
    box-shadow: 0 0 15px rgba(0, 255, 252, 0.2);
    transition: all 0.3s ease;
}

.timeline-content:hover {
    transform: translateY(-5px);
    box-shadow: 0 5px 20px rgba(0, 255, 252, 0.4);
}

.timeline-content h3 {
    color: #00fffc;
    margin-bottom: 0.5rem;
}

.timeline-content h4 {
    color: #ff00fc;
    margin-bottom: 0.5rem;
}

.timeline-date {
    font-size: 0.9rem;
    color: #00fffc;
    margin-bottom: 1rem;
}

/* Add a glitch effect to the timeline items */
@keyframes glitch {
    0% {
        clip-path: inset(0 0 0 0);
    }
    2%, 4%, 6% {
        clip-path: inset(30% 0 0 0);
        transform: translate(-5px, -5px);
    }
    8%, 10%, 12% {
        clip-path: inset(0 30% 0 0);
        transform: translate(5px, 5px);
    }
    14%, 16%, 18% {
        clip-path: inset(0 0 30% 0);
        transform: translate(5px, -5px);
    }
    20%, 22%, 24% {
        clip-path: inset(0 0 0 30%);
        transform: translate(-5px, 5px);
    }
    26% {
        clip-path: inset(0 0 0 0);
        transform: translate(0);
    }
}

.timeline-content:hover::before {
    content: attr(data-text);
    position: absolute;
    left: -2px;
    top: -2px;
    width: calc(100% + 4px);
    height: calc(100% + 4px);
    background: rgba(0, 255, 252, 0.2);
    animation: glitch 2s infinite linear alternate-reverse;
}

/* Responsive design */
@media screen and (max-width: 768px) {
    .timeline::before {
        left: 20px;
    }
    .timeline {
       
        left: 20px;
    }
    .timeline-item {
        width: 80%;
        padding-left: 30px;
        padding-right: 0;
        text-align: left;
    }
    .timeline-icon {
        top: 1rem;
    }
    .timeline-item:nth-child(odd) {
        float: none;
        padding-right: 0;
        text-align: left;
    }
    
    .timeline-item:nth-child(even) {
        float: none;
    }
    
    .timeline-icon {
        left: -5px !important;
    }
}
