  box-shadow: 0 0 15px rgba(0, 255, 252, 0.5);
  color: #ffffff;
}

.project-link:hover::before {
  left: 100%;
}

/* Enhanced Coding Profiles Section */
.coding-profiles-section {
  position: relative;
  overflow: hidden;
  background: rgba(0, 0, 0, 0.5);
}

.coding-profile-card {
  position: relative;
  overflow: hidden;
  transition: all 0.4s ease;
  border: 1px solid rgba(0, 255, 252, 0.3);
}

.coding-profile-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    45deg,
    rgba(0, 255, 252, 0.1) 0%,
    rgba(255, 0, 252, 0.1) 100%
  );
  opacity: 0;
  transition: opacity 0.4s ease;
}

.coding-profile-card:hover {
  transform: translateY(-5px);
  box-shadow: 
    0 5px 15px rgba(0, 255, 252, 0.3),
    0 0 20px rgba(255, 0, 252, 0.2);
  border: 1px solid rgba(0, 255, 252, 0.6);
}

.coding-profile-card:hover::before {
  opacity: 1;
}

.profile-header {
  transition: all 0.3s ease;
}

.coding-profile-card:hover .profile-header {
  text-shadow: 0 0 10px rgba(0, 255, 252, 0.7);
}

.coding-profile-card:hover .social-icon {
  transform: scale(1.2);
  box-shadow: 0 0 15px rgba(0, 255, 252, 0.5);
}


#about {
  background-color: #000000;
  color: #f5f5f5;
  padding: 3rem;
  margin: 2rem auto;
  border: 2px solid transparent;
  border-radius: 12px;
  transition: all 0.3s ease-in-out;
  box-shadow: 0 0 10px rgba(0, 255, 255, 0.3);
  max-width: 900px;
  position: relative;
  z-index: 1;
}

#about:hover {
  border-image: linear-gradient(135deg, #39afaf, #ff00ff) 1;
  box-shadow: 
    0 0 10px #00ffffaa,
    0 0 20px #ff00ffaa,
    0 0 30px #00ffff88;
}

.section-title {
  font-size: 2.5rem;
  color: #00ffff;
  text-shadow: 0 0 10px #00ffff;
  margin-bottom: 1.5rem;
  font-weight: 600;
  letter-spacing: 2px;
}

.about-content p {
  font-size: 1.1rem;
  line-height: 1.8;
  color: #ccc;
  margin-bottom: 1rem;
  text-shadow: 0 0 4px #ff00ff33;
}
